// 基础类接口 数据
import {
  isSchoolTest,
  getAreaList,
  getCityList,
  getClassType,
  getLinkage,
  getServiceCenterList,
  getTeacherByNameFuzzy,
  getTimeList,
  getTimeTypeList,
  getVenueList,
  getYearList
} from '@/api/bd'
export default {
  data () {
    return {
      allCityList: [],
      allCityMap: {}, // 分校
      classtimeList: [],
      classtimeTypeList: [],
      classTypeList: [],
      isSchoolTestList: [],
      yearList: []
    }
  },
  methods: {
    // 是否进行入学测试查询
    isSchoolTestHandler () {
      let req = {}
      isSchoolTest(req).then(res => {
        this.isSchoolTestList = Object.freeze(res)
      }).catch(err => {
        console.log(err)
        this.$Message.warning({ content: '获取是否入学测试失败:' + err.message, duration: 5 })
      })
    },
    // 获取区域
    getAreaListHandler (params) {
      return getAreaList(params)
    },
    // 获取所有城市
    getCityListHandler () {
      getCityList().then((data) => {
        this.allCityList = data
        // this.allCityList.forEach((e) => {
        //   this.allCityMap[e.cityCode] = e.name
        // })
      }).catch((err) => {
        this.$Message.warning({ content: '获取所有城市失败:' + err.message, duration: 5 })
      })
    },
    // 授课类型
    getClassTypeHandler () {
      let req = {}
      getClassType(req).then(res => {
        this.classTypeList = Object.freeze(res)
      }).catch(err => {
        this.$Message.warning({ content: '获取授课类型失败:' + err.message, duration: 5 })
      })
    },
    getLinkageHandler (params) {
      return getLinkage(params)
    },
    getServiceCenterListHandler (params) {
      return getServiceCenterList(params)
    },
    getTeacherByNameFuzzyHandler (params) {
      return getTeacherByNameFuzzy(params)
    },
    // 上课时间类型查询
    getTimeTypeListHandler () {
      let req = {}
      getTimeTypeList(req).then(res => {
        this.classtimeTypeList = Object.freeze(res)
      }).catch(err => {
        this.$Message.warning({ content: '获取上课时间类型失败:' + err.message, duration: 5 })
      })
    },
    // 上课时间查询
    getTimeListHandler () {
      let req = {}
      getTimeList(req).then(res => {
        this.classtimeList = Object.freeze(res)
      }).catch(err => {
        this.$Message.warning({ content: '获取上课时间失败:' + err.message, duration: 5 })
      })
    },
    getVenueListHandler (params) {
      return getVenueList(params)
    },
    // 获取年份
    getYearListHandler () {
      getYearList().then((data) => {
        this.yearList = Object.freeze(data)
      }).catch((err) => {
        this.$Message.warning({ content: '获取年份失败:' + err.message, duration: 5 })
      })
    }

  }
}
