<template>
  <Layout id="news-list">
    <Row class='search'>
      <Select
        v-model="cityCode"
        filterable
        size='small'
        style="width: 120px;margin-right: 10px"
        placeholder='请选择城市'>
        <Option v-for="(option,index) in allCityList" :value="option.id" :key="index">{{option.name}}</Option>
      </Select>
      <Button
        type="success"
        size='small'
        :disabled='!cityCode'
        :loading="loading"
        style="width: 100px;"
        @click="cityChangeHandle">查询</Button>
    </Row>
    <TalIframe :content-src="dataSrc" />
  </Layout>
</template>

<script>
import { genManage } from '@/api/news'
import BaseDataMixin from '@/mixins/baseData'
import TalIframe from '@/components/iframe'
// import Cookie from 'js-cookie'
export default {
  name: 'newsList',
  data () {
    return {
      dataSrc: '',
      cityCode: '',
      loading: false
    }
  },
  components: { TalIframe },
  mixins: [BaseDataMixin],
  mounted () {
    this.getCityListHandler()
  },
  methods: {
    cityChangeHandle () {
      this.loading = true
      this.dataSrc = ''
      genManage({
        cityCode: this.cityCode
      }).then(data => {
        this.dataSrc = data.url
      }).catch(err => {
        this.$Message.error({
          content: err.message,
          duration: 5
        })
      }).finally(() => {
        this.loading = false
      })
    }
  }
}
</script>
<style lang='less'>
  #news-list {
    background-color: #f9fafb;
    .search {
      padding: 10px 0 0 10px;
      margin-bottom: -10px;
    }
  }
</style>
